import * as React from "react";
import { Tabs, AppBar, Tab } from "@material-ui/core";
import { RankingTab } from "./tabs";
import { useTranslation } from "react-i18next";

interface IProps {
  currentTab: string;
  handleTabChange: (tab: string) => void;
}

interface ITabProps {
  tabRoute: string;
  translationKey: string;
  component: () => React.ReactElement;
}

const tabs: ITabProps[] = [
  {
    tabRoute: "rankings",
    translationKey: "pages.event.subpages.rankings",
    component: () => <RankingTab />
  }
];

const EventTabs = function ({ currentTab, handleTabChange }: IProps) {
  const { t } = useTranslation();
  return (
    <div>
      <AppBar position='static'>
        <Tabs value={currentTab} variant='scrollable' textColor='primary' indicatorColor='primary' scrollButtons='auto'>
          {tabs.map((tab: ITabProps) => {
            return <Tab label={t(tab.translationKey + ".title")} />;
          })}
        </Tabs>
      </AppBar>
      {tabs.map((tab: ITabProps) => {
        return <tab.component key={`tab-${tab.tabRoute}`}></tab.component>;
      })}
    </div>
  );
};

export default EventTabs;
